<template>
  <div class="wrap">
    <div id="app">
      <div class="top">
        <div class="title">{{ message }}</div>
        <div class="search">
          <img
            src="../../public/images/zoom.png"
            @click="searchMusic"
            style="cursor: pointer"
          />
          <input
            type="text"
            autocomplete="off"
            placeholder="毛不易"
            v-model="query"
            @keyup.enter="searchMusic"
          />
        </div>

        <div class="content">
          <div class="left_musicList">
            <ul class="song_List">
              <!-- <li v-bind:key="item in musicList"> -->
              <li v-for="(item, index) in musicList" :key="index">
                <a href="javascript:;" @click="playMusic(item.id)">
                  <img
                    src="../../public/images/play.png"
                    class="playMusic"
                    @click="playMusic"
                  />
                </a>
                <b>{{ item.name }}</b>
                <span
                  ><i v-show="item.mvid != 0" @click="playMv(item.mvid)"></i
                ></span>
              </li>
            </ul>
          </div>

          <div class="mid_anniamtion" :class="{ playing: isPlaying }">
            <img src="../../public/images/disc.png" class="disc autoRotate" />
            <img :src="picUrl" class="cover autoRotate" />
            <div class="player_bar">
              <img src="../../public/images/player_bar.png" alt="" />
            </div>
            <img src="../../public/images/line.png" class="switch_Lbtn" />
            <img
              src="../../public/images/line.png"
              alt=""
              class="switch_Rbtn"
            />
          </div>

          <div class="right_comment">
            <h4>热门留言</h4>
            <ul class="review_List">
              <li v-for="(item, index) in commentList" :key="index">
                <img :src="item.user.avatarUrl" alt="" />
                <b>{{ item.user.nickname }}</b
                ><br /><span>{{ item.content }}</span>
              </li>
            </ul>
          </div>
          <div class="clearFloat" style="clear: both"></div>

          <div class="audio_con">
            <audio
              ref="audio"
              :src="musicUrl"
              @play="play"
              @pause="pause"
              controls="controls"
              autoplay="autoplay"
              loop="loop"
              class="myaudio"
            ></audio>
          </div>
        </div>
        <div class="media" v-show="isShow">
          <video
            ref="video"
            :src="mvid"
            controls="controls"
            preload="auto"
            autoplay="autoplay"
            class="videoPlay"
          ></video>
          <div class="mask" @click="maskHide"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import cover from '../../src/assets/cover.png'
export default {
  data() {
    return {
      imgCover:cover,
      message: "网抑云",
      query: "",
      place: "毛不易",
      musicList: [],
      mvid: "",
      musicUrl: "",
      picUrl: cover,
      commentList: [],
      isPlaying: false,
      isShow: false,
    };
  },
  created() {
    this.searchMusic();
    this.query = "";
  },
  computed: {},
  methods: {
    searchMusic() {
      if (this.query == "") {
        this.query = "毛不易";
      }
      this.axios
        .get("https://autumnfish.cn/search?keywords=" + this.query)
        .then(
          (res) => {
            // console.log(res);
            this.musicList = res.data.result.songs;
          }
          // function (response) {
          //   that.musicList = response.data.result.songs;
          //   // console.log(that.musicList);
          // },
          // function (err) {}
        )
        .catch((err) => {
          console.log(err);
        });
    },
    playMusic: function (musicId) {
      var that = this;
      this.axios.get("https://autumnfish.cn/song/url?id=" + musicId).then(
        function (response) {
          that.musicUrl = response.data.data[0].url;
        },
        function (err) {}
      );

      this.axios.get("https://autumnfish.cn/song/detail?ids=" + musicId).then(
        function (response) {
          that.picUrl = response.data.songs[0].al.picUrl;
          console.log(response.data.songs[0].al.picUrl);
        },
        function (err) {}
      );

      this.axios
        .get("https://autumnfish.cn/comment/hot?type=0&id=" + musicId)
        .then(
          function (response) {
            // console.log(response.data.hotComments);
            that.commentList = response.data.hotComments;
          },
          function (err) {}
        );
      this.$refs["audio"].play();
    },
    play: function () {
      this.isPlaying = true;
    },
    pause: function () {
      this.isPlaying = false;
    },
    playMv: function (mvid) {
      this.$refs["audio"].pause();
      var that = this;
      this.axios.get("https://autumnfish.cn/mv/url?id=" + mvid).then(
        function (response) {
          // console.log(response.data.data.url);
          that.mvid = response.data.data.url;
          that.isShow = true;
        },
        function (err) {}
      );
    },
    maskHide: function () {
      this.isShow = false;
      this.$refs["video"].pause();
      this.$refs["audio"].play();
    },
  },
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: none;
}

.wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../../public/images/bg.jpg) no-repeat;
  background-size: 100% 100%;
}

#app {
  /* width: 100%; */
  position: relative;
  min-height: calc(100vh - 300px);
  display: block;
  width: 1184px;
  /* height: 846px; */
  margin: 0 auto;
  /* margin-left: 150px; */
}

.top {
  position: relative;
  width: 100%;
  height: 100px;
  background-color: #1eacda;
  border-radius: 2em 2em 0 0;
}

.title {
  padding: 15px;
  padding-left: 50px;
  font-size: 50px;
  color: white;
  margin-top: 50px;
}

.search {
  position: absolute;
  top: 25px;
  right: 25px;
  border-radius: 50px;
  width: 30%;
  height: 50px;
  background-color: powderblue;
}

.search img {
  width: 25px;
  height: 25px;
  float: right;
  padding: 13px 20px;
}

.search input {
  position: absolute;
  top: 7%;
  left: 5%;
  width: 70%;
  height: 80%;
  font-size: 20px;
  color: black;
  /* border-color: powderblue; */
  /* cursor: no-drop; */
  box-shadow: 40px 40px 40px 40px powderblue inset;
  border: 1px solid powderblue;
  outline: none;
}

.content {
  height: 700px;
  width: 100%;
  display: flex;
  position: relative;
  background-color: rgba(255, 255, 255, 0.5);
  z-index: 6;
}

.left_musicList {
  position: relative;
  width: 25%;
  height: 100%;
  /* background-color: rgba(255, 255, 255, 0.5);; */
  float: left;
  overflow: hidden;
  overflow-y: scroll;
  box-sizing: border-box;
  /* padding: 3px 0 0 3px; */
}

/* 去掉滚动条样式 */
.left_musicList::-webkit-scrollbar {
  width: 0 !important;
  height: 0 !important;
}

/* 去掉滚动条样式 */
.right_comment::-webkit-scrollbar {
  width: 0 !important;
  height: 0 !important;
}

.playMusic {
  width: 30px;
  height: 30px;
  padding-right: 10px;
}

.song_List li:nth-child(odd) {
  background-color: rgba(240, 240, 240, 0.3);
}

.song_List li span {
  width: 23px;
  height: 17px;
  /* margin-right: 50px; */
}

.song_List li span i {
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
  background: url(../../public/images/table.png) left -48px no-repeat;
}

.playMv {
  width: 30px;
  height: 30px;
  padding: 0 10px 0 0;
}

.switch_Lbtn {
  position: absolute;
  left: 0;
  top: 0;
  height: auto;
  cursor: pointer;
  height: 100%;
}

.switch_Rbtn {
  position: absolute;
  right: 0;
  top: 0;
  height: auto;
  cursor: pointer;
  height: 100%;
}

.song_List li {
  display: flex;
  color: #333;
  /* width: 229.75px; */
  height: 40px;
  list-style: none;
  align-items: center;
  padding: 10px;
}

.left_musicList a {
  text-decoration: none;
}

.left_musicList b {
  width: 200px;
  text-overflow: ellipsis;
  font-weight: normal;
  white-space: nowrap;
  font-size: 20px;
  color: black;
  overflow: hidden;
}

.mid_anniamtion {
  position: relative;
  float: left;
  width: 50%;
  height: 100%;
  /* background-color:cornsilk; */
}

.disc {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -200px;
  margin-left: -115px;
  z-index: 5;
}

.cover {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 170px;
  width: 170px;
  margin-top: -160px;
  margin-left: -75px;
}

.player_bar {
  position: absolute;
  top: 12%;
  left: 50%;
  z-index: 8;
  transform: rotate(-60deg);
  transform-origin: 12px 12px;
  transition: 1s;
}

.right_comment {
  float: left;
  width: 25%;
  /* height: 100%; */
  margin-top: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  overflow: hidden;
  overflow-y: scroll;
  /* background-color:cadetblue; */
}

.right_comment img {
  width: 40px;
  height: 40px;
}

.right_comment li {
  position: relative;
  list-style: none;
  padding: 10px;
  /* padding-top: 0; */
}

.right_comment b {
  position: absolute;
  top: 20px;
  left: 70px;
}

.right_comment span {
  font-size: 16px;
  text-overflow: ellipsis;
  display: block;
  margin-inline-start: 60px;
  color: #666;
}

/* 旋转的动画 */
@keyframes Rotate {
  from {
    transform: rotateZ(0);
  }
  to {
    transform: rotateZ(360deg);
  }
}

.mid_anniamtion.playing .player_bar {
  transform: rotate(0);
}

.barRotate {
  transform: rotate(-60deg);
  transform-origin: left top;
}

.autoRotate {
  animation-name: Rotate;
  animation-iteration-count: infinite;
  animation-play-state: paused;
  animation-timing-function: linear;
  animation-duration: 5s;
}

/* 是否正在播放 */
.mid_anniamtion.playing .disc,
.mid_anniamtion.playing .cover {
  animation-play-state: running;
}

.audio_con {
  height: 50px;
  background-color: #f1f3f4;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  margin-bottom: -50px;
}

.myaudio {
  width: 1184px;
  height: 40px;
  margin-top: 5px;
  outline: none;
  background-color: #f1f3f4;
}

.media {
  position: absolute;
  top: 0;
  left: 0;
  min-height: calc(100vh - 300px);
  display: block;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  z-index: 999;
  background-color: #666;
}

.media video {
  position: fixed;
  width: auto;
  height: 851px;
  left: 50%;
  top: 50%;
  transform: translateX(-50%);
  margin-top: -420px;
  z-index: 990;
}

.media .mask {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 980;
  background-color: rgba(0, 0, 0, 0.8);
}
.meida .shutoff {
  position: fixed;
  width: 40px;
  height: 40px;
  background: url("../../public/images/shutoff.png") no-repeat;
  left: 50%;
  margin-left: 400px;
  margin-top: -273px;
  top: 50%;
  z-index: 995;
}
</style>

